@import "./sass/colors";
@import "./sass/fonts";

.login-page {
  font-family: 'Open Sans', sans-serif;
  background-color: $-tan;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.login-container {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 600px;
  overflow: hidden;
}

.login-header {
  background-color: $-dark-green;
  color: white;
  padding: 30px 20px;
  text-align: center;

  h1 {
    font-family: 'Virust', 'Open Sans', sans-serif;
    font-size: 42px;
    margin-bottom: 10px;
  }

  p {
    font-size: 28px;
    opacity: 0.9;
  }
}

.login-form {
  padding: 30px 25px;

  form {
    width: 100%;
  }
}

.form-group {
  margin-bottom: 20px;

  label {
    display: block;
    margin-bottom: 8px;
    color: $-dark-green;
    font-weight: 600;
  }

  .input-wrapper {
    position: relative;
    
    .input-icon {
      position: absolute;
      left: 12px;
      top: 50%;
      transform: translateY(-50%);
      color: #6b7280;
    }
  }

  input {
    width: 100%;
    padding: 12px 15px 12px 40px;
    border: 2px solid #ddd;
    border-radius: 6px;
    font-size: 16px;
    transition: border 0.3s;

    &:focus {
      border-color: $-green;
      outline: none;
    }
  }
}

.forgot-password {
  display: block;
  text-align: right;
  margin-top: 5px;
  color: $-green;
  text-decoration: none;
  font-size: 14px;

  &:hover {
    text-decoration: underline;
  }
}

.login-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background-color: $-green;
  color: white;
  border: none;
  padding: 12px 0;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  margin-top: 10px;
  transition: background-color 0.3s;

  &:hover {
    background-color: $-dark-green;
  }
}

.divider {
  display: flex;
  align-items: center;
  margin: 25px 0;

  &::before, 
  &::after {
    content: "";
    flex-grow: 1;
    background-color: #ddd;
    height: 1px;
  }

  span {
    padding: 0 15px;
    font-size: 14px;
    color: #888;
  }
}

.oauth-container {
  display: flex;
  justify-content: center;
}

.google-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  color: #757575;
  border: 2px solid #ddd;
  padding: 12px 20px;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s, border-color 0.3s;
  width: 100%;

  &:hover {
    background-color: #f5f5f5;
    border-color: #ccc;
  }

  .google-icon {
    margin-right: 10px;
  }
}

.signup-link {
  text-align: center;
  margin-top: 25px;
  font-size: 14px;
  color: #666;

  a {
    color: $-rust-brown;
    text-decoration: none;
    font-weight: 600;

    &:hover {
      text-decoration: underline;
    }
  }
}